    <div v-else-if="lang===`en`">
      <h4 class="fw-bold">A blow to the "underbelly" of Festung Europa</h4>
      <p class="fw-bold">
        The victory of the Allies over the German and Italian troops in North Africa in May 1943 made the opening of the so-called the second front in Europe. However, discussions continued on where the main invasion forces should land: on the shores of the Mediterranean Sea or the Le Manche Channel.
      </p>
      <p>
        Allied forces were gathering in the conquered Tunis as well as in Great Britain and their commanders waited for the signal to attack. The Americans, and especially General George C. Marshall, then Chief of the General Staff of the US Army, called for a strike from England through the English Channel and from France to move towards Berlin. Stalin was also a great supporter of this option, constantly demanding that the Red Army be relieved by opening a second front in Western Europe. On the other hand, Winston Churchill and his generals insisted that the main strike be directed through the Balkans. The British Prime Minister, in his characteristic way, called it "the soft underbelly of Europe". In Churchill's calculations, the invasion from southern Europe allowed to overtake the Soviets heading towards the Vistula and Danube, and also to protect Yugoslavia and Greece from the specter of communism. Moreover, it would detach from the Third Reich its main allies in Europe: Italy, Romania, Bulgaria and Hungary, and activate Turkey to side with the Allies. However, at the Casablanca conference in January 1943, Churchill let himself be persuaded by Franklin D. Roosevelt and agreed to a compromise. He stopped insisting on the Balkans in favor of an invasion of France in 1944, but on the condition that an auxiliary strike would be launched earlier in southern Italy. Eliminating Italy from the war would force the Germans to transfer some of their troops to the Apennine Peninsula, and thus disperse their forces in Europe. Churchill also secretly hoped that his quick success in Italy would allow him to implement the Balkan scenario and strengthen British influence in the Mediterranean.



      </p>
      <h4 class="fw-bold"> Sicilian summer</h4>
      <p>
        In May 1943, during a conference in Washington, the final decisions were made on the landing on Italy, which was hidden under the code name "Trident". His prelude was to be the landing in Sicily, ie Operation Husky. On July 10, 1943, the US 7th Army of General George S. Patton and the British 8th Army of General Bernard L. Montgomery landed on the beaches of Sicily. The invasion from the sea was preceded by a night air landing and paratroopers were the first to discover how capricious and treacherous Italian weather can be. Strong winds scattered them far from the drop zones, and for the next few days many of them struggled through the enemy lines. The Italians, and especially the Germans, also surprised the Allies with their tough defense, skillfully commanded by Marshal Albert Kesselring. In the rocky and ravine-cut terrain, the Italian and German soldiers fought fiercely and skillfully. The Americans and the British gained every position after the complete destruction of the defenders. However, in late July, Marshal Kesselring realized that the island was unsustainable. The advantage of the Allies grew every day, and especially the actions of General Patton began to threaten to cut off the troops stubbornly defending the Mount Etna from Messina. Eventually, Kesselring ordered the evacuation of Sicily, which began in the first days of August. About 45,000 Germans and about 60,000 Italians managed to evacuate through the Strait of Messina to Calabria. On August 17, General Patton's armored vehicles entered Messina, followed by Montgomery's 8th Army tankers. The conquest of the city crowned 38 days of fierce fighting for Sicily, during which the Allies lost 20,000 killed, wounded and missing. The Italian-German army had several thousand dead, and 150,000, mainly Italians, were captured. Immediately after capturing Messina, the Allies began shelling enemy positions and coastal batteries in Calabria. Allied battleships, cruisers and destroyers also entered the action, telling the Germans and Italians with their fire that the invasion of the Apennine Peninsula was about to start.


      </p>
      <h4 class="fw-bold"> Breakage of the fascist axis</h4>
      <p>
        On September 3, 1943, two British 8th Army divisions crossed the Strait of Messina and easily seized the "tip of the Italian shoe" in the Calabria region. German and Italian troops quickly retreated deep into the peninsula. The Germans expected an Allied landing, but they were surprised by the unexpected volley of the Italians - the overthrow of Mussolini, capitulation and the transition to the Allied side. However, they quickly and decisively responded to this deconstruction of the so-called Axis Pact. German troops quickly seized the garrisons and positions of the Italian army. Any resistance was liquidated by force, and the surprised Italians were disarmed and taken prisoner. Thousands of Italian soldiers were sent to prisoner of war and concentration camps, many were murdered. The Allies found out how effective the Wehrmacht is still in Italy on 9 September near Salerno, where an avalanche of irons fired by German tanks hit the American and British troops landing on the beaches. The air force who attacked them saved them from the catastrophe, which was weakened by the armored strike. It became clear, however, that walking through the "Italian Shoe" would not be a walk. By September 15, the German attacks near Salerno had lost their momentum in the fire of the Allied attack planes and bombers. The Germans, however, moved back northwards slowly and in an organized manner - they proved once again that they were masters of defense. Meanwhile, the British Eighth Army marched from Calabria and its left wing joined the amphibious troops south of Salerno, and the left one moved towards Foggia. On the other hand, the Americans from Salerno attacked Naples, where an anti-German uprising broke out. In early October, the city was in the hands of the soldiers of General Mark Clark, commander of the American 5th Army. The battles for Foggia ended with a similar success, where a great airport was captured. On the other hand, the port in Naples was efficiently launched, which was very useful to the Allies during the entire Italian campaign.
      </p>
      <h4 class="fw-bold">"Sunny Italia" - it's a mockery!</h4>
      <p>
        Unfortunately, autumn came in Italy, and with it terrible weather, which favored the defenders, i.e. the Germans, and turned the actions of the Allied armies into a real nightmare. Soldiers, flogged with rain and wind, slogging in the mud ironically repeated slogans about "sunny Italy". Allied Commander-in-Chief, General Dwight Eisenhower recalled: “Even if we did not meet enemy resistance, it would be difficult to move along the destroyed railroad tracks, demolished bridges, and blown up road sections. The terrain itself, cut by large and small rivers that ran across the routes of our attack, was perfect for defensive combat. Some rivers were so winding that we had to cross them several times. " That's it. And around the bend of every road and river, stronger and stronger Germans waited for the Allied soldiers. The British from the 8th Army walked slower and slower, and finally got stuck on the so-called the winter line - German defensive positions, the main elements of which were the mountain rivers Garigliano and Sangro and the peaks of the Apennines. Positions on the mountain passes in the autumn weather seemed impregnable to the Allies, so their forces diverged both ways: the American 5th Army was breaking through the enemy positions along the shores of the Tyrrhenian Sea, and the British 8th Army was advancing along the Adriatic. The Apennines lying between them completely prevented cooperation between them, and each attempt ended in heavy losses in the fire of German paratroopers and alpine riflemen controlling the mountains. The Germans in southern Italy were also gaining strength thanks to new divisions arriving from the north of the country and Germany. It became clear to the Allied command that the elimination of Italy as an ally of the Third Reich only for a moment shook the still powerful and battle-hardened German troops. It was decided to call for reinforcements: the French Expeditionary Corps, among others, were transported from North Africa and it was decided that the 2nd Polish Corps would also be transferred to Italy. In addition, the 15th Strategic Air Fleet was brought in.
      </p>
      <h4 class="fw-bold">Million Dollar Mountain</h4>
      <p>
        One of the British war correspondents wrote from Italy in late 1943: "Our men forced Sangro, but behind Sangro was Moro, behind Moro was Foro, and behind Foro was Pescara ...". And there was no end in sight. However, before the 8th Army reached the Pescara River and the city of the same name on the Adriatic coast, it had to capture the small town of Ortone. This sentence fell to the First Canadian Division, and the Canadians were the first to find out what the enemy had in store on the way to Rome. British WWII veteran and writer described the battle this way: “You had to conquer every street, every house and even every floor here. [The Canadians - PK] It took […] this week, they spent Christmas this way and finally they won. But that was the end of the 8th Army's effort. Pescara was ten miles, the weather and terrain were worse than ever and the army had to temporarily abandon anchor. " On the opposite side of the Apennine Peninsula, the American 5th Army marched. The Americans, like the British, fought mud and an increasingly tougher enemy. They managed to reach Monte Camino with great difficulty. During the fights for this hill, artillery fired 200,000 shells. So American soldiers called it the Million Dollar Mountain. On January 15, 1944, in the area of the Naples - Rome road, they captured Monte Trocchio. So, counting the space and time from the conquest of Naples, it took the Allies two months to walk several dozen kilometers… And ahead of them arose the mountain ranges of Maiella, Abruzzi and Aurunci, dividing the peninsula from the Adriatic Sea to the Tyrrhenian Sea. Here the Germans carved and concrete two powerful defense lines: Gustav and Hitler. The epicenter of Hell awaited the Allies in Cassino and the surrounding area.
      </p>
