    <div v-else-if="lang===`it`">
      <h4 class="fw-bold">Un duro colpo al "ventre molle" di Festung Europa</h4>
      <p class="fw-bold">
        La vittoria degli Alleati sulle truppe tedesche e italiane in Nord Africa nel maggio 1943 fece aprire il cosiddetto secondo fronte in Europa. Tuttavia, le discussioni sono continuate su dove dovrebbero sbarcare le principali forze di invasione: sulle rive del Mar Mediterraneo o sul Canale di Le Manche.


      </p>
      <p>
       Le forze alleate si stavano radunando nella Tunisi conquistata così come in Gran Bretagna ei loro comandanti aspettavano il segnale per attaccare. Gli americani, e soprattutto il generale George C. Marshall, allora capo di stato maggiore dell'esercito americano, chiesero uno sciopero dall'Inghilterra attraverso la Manica e dalla Francia per spostarsi verso Berlino. Anche Stalin fu un grande sostenitore di questa opzione, chiedendo costantemente che l'Armata Rossa fosse sollevata aprendo un secondo fronte nell'Europa occidentale. D'altra parte, Winston Churchill ei suoi generali hanno insistito affinché lo sciopero principale fosse diretto attraverso i Balcani. Il primo ministro britannico, nel suo modo caratteristico, l'ha definito "il ventre molle dell'Europa". Nei calcoli di Churchill, l'invasione dall'Europa meridionale ha permesso di superare i sovietici diretti verso la Vistola e il Danubio, e anche di proteggere la Jugoslavia e la Grecia dallo spettro del comunismo. Inoltre, avrebbe distaccato dal Terzo Reich i suoi principali alleati in Europa: Italia, Romania, Bulgaria e Ungheria, e avrebbe attivato la Turchia a fianco degli Alleati. Tuttavia, alla conferenza di Casablanca nel gennaio 1943, Churchill si lasciò persuadere da Franklin D. Roosevelt e accettò un compromesso. Smise di insistere sui Balcani a favore di un'invasione della Francia nel 1944, ma a condizione che uno sciopero ausiliario fosse lanciato prima nell'Italia meridionale. L'eliminazione dell'Italia dalla guerra costringerebbe i tedeschi a trasferire parte delle loro truppe nella penisola appenninica, disperdendo così le loro forze in Europa. Churchill sperava anche segretamente che il suo rapido successo in Italia gli avrebbe permesso di attuare lo scenario balcanico e rafforzare l'influenza britannica nel Mediterraneo.


      </p>
      <h4 class="fw-bold"> Estate siciliana</h4>
      <p>
        Nel maggio 1943, durante una conferenza a Washington, furono prese le decisioni finali sullo sbarco in Italia, nascosto sotto il nome in codice "Tridente". Il suo preludio doveva essere lo sbarco in Sicilia, ovvero l'Operazione Husky. Il 10 luglio 1943, la 7a armata americana del generale George S. Patton e l'8a armata britannica del generale Bernard L. Montgomery sbarcarono sulle spiagge della Sicilia. L'invasione dal mare fu preceduta da un atterraggio aereo notturno ei paracadutisti furono i primi a scoprire quanto possa essere capriccioso e insidioso il clima italiano. Forti venti li disperdevano lontano dalle zone di lancio e nei giorni successivi molti di loro lottarono attraverso le linee nemiche. Gli italiani, e soprattutto i tedeschi, sorpresero anche gli alleati con la loro dura difesa, abilmente comandata dal maresciallo Albert Kesselring. Sul terreno roccioso e scavato da burroni, i soldati italiani e tedeschi combatterono ferocemente e abilmente. Gli americani e gli inglesi hanno guadagnato ogni posizione dopo la completa distruzione dei difensori. Tuttavia, alla fine di luglio, il maresciallo Kesselring si rese conto che l'isola era insostenibile. Il vantaggio degli Alleati cresceva di giorno in giorno, e soprattutto le azioni del generale Patton iniziarono a minacciare di tagliare le truppe che ostinatamente difendevano l'Etna da Messina. Alla fine, Kesselring ordinò l'evacuazione della Sicilia, iniziata nei primi giorni di agosto. Circa 45.000 tedeschi e circa 60.000 italiani sono riusciti a evacuare attraverso lo Stretto di Messina in Calabria. Il 17 agosto, i veicoli corazzati del generale Patton entrarono a Messina, seguiti dalle petroliere dell'8a armata di Montgomery. La conquista della città coronò 38 giorni di aspri combattimenti per la Sicilia, durante i quali gli Alleati persero 20.000 morti, feriti e dispersi. L'esercito italo-tedesco aveva diverse migliaia di morti e 150.000, principalmente italiani, furono catturati. Immediatamente dopo aver catturato Messina, gli Alleati iniziarono a bombardare le posizioni nemiche e le batterie costiere in Calabria. Anche corazzate, incrociatori e cacciatorpediniere alleati entrarono in azione, dicendo a tedeschi e italiani con il loro fuoco che stava per iniziare l'invasione della penisola appenninica.


      </p>
      <h4 class="fw-bold"> Rottura dell'asse fascista</h4>
      <p>
       Il 3 settembre 1943, due divisioni dell'8a armata britannica attraversarono lo Stretto di Messina e si impadronirono facilmente della "punta della scarpa italiana" nella regione Calabria. Le truppe tedesche e italiane si ritirarono rapidamente in profondità nella penisola. I tedeschi si aspettavano uno sbarco alleato, ma furono sorpresi dalla raffica inaspettata degli italiani: il rovesciamento di Mussolini, la capitolazione e il passaggio al lato alleato. Tuttavia, hanno risposto rapidamente e con decisione a questa decostruzione del cosiddetto Patto dell'Asse. Le truppe tedesche presero rapidamente le guarnigioni e le posizioni dell'esercito italiano. Ogni resistenza fu liquidata con la forza e gli italiani sorpresi furono disarmati e fatti prigionieri. Migliaia di soldati italiani furono mandati in prigionieri di guerra e campi di concentramento, molti furono assassinati. Gli alleati hanno scoperto l'efficacia della Wehrmacht ancora in Italia il 9 settembre nei pressi di Salerno, dove una valanga di ferri sparati dai carri armati tedeschi ha colpito le truppe americane e britanniche che sbarcano sulle spiagge. L'aeronautica militare che li ha attaccati li ha salvati dalla catastrofe, indebolita dall'attacco corazzato. È diventato chiaro, tuttavia, che camminare attraverso la "scarpa italiana" non sarebbe stata una passeggiata. Entro il 15 settembre, gli attacchi tedeschi vicino a Salerno avevano perso il loro slancio nel fuoco degli aerei d'attacco e dei bombardieri alleati. I tedeschi, tuttavia, tornarono indietro verso nord lentamente e in modo organizzato: dimostrarono ancora una volta di essere maestri della difesa. Intanto l'ottava armata britannica marciava dalla Calabria e la sua ala sinistra si unì alle truppe anfibie a sud di Salerno, e quella sinistra si mosse verso Foggia. Gli americani salernitani, invece, attaccarono Napoli, dove scoppiò una rivolta anti-tedesca. Ai primi di ottobre, la città era nelle mani dei soldati del generale Mark Clark, comandante della 5a armata americana. Con analogo successo si concluse le battaglie per Foggia, dove fu catturato un grande aeroporto. E 'stato invece efficacemente varato il porto di Napoli, utilissimo agli Alleati durante tutta la campagna d'Italia.
      </p>
      <h4 class="fw-bold">"Sunny Italia" - è una presa in giro!</h4>
      <p>
        Purtroppo in Italia arrivò l'autunno, e con esso il tempo terribile, che favorì i difensori, cioè i tedeschi, e trasformò le azioni degli eserciti alleati in un vero incubo. Soldati, flagellati dalla pioggia e dal vento, che arrancano nel fango ripetono ironicamente slogan sulla "soleggiata Italia". Il comandante in capo alleato, il generale Dwight Eisenhower ha ricordato: “Anche se non incontrassimo la resistenza nemica, sarebbe difficile muoverci lungo i binari della ferrovia distrutti, i ponti demoliti e le sezioni stradali fatte saltare in aria. Il terreno stesso, tagliato da fiumi grandi e piccoli che scorrevano lungo le rotte del nostro attacco, era perfetto per il combattimento difensivo. Alcuni fiumi erano così tortuosi che abbiamo dovuto attraversarli più volte. "Questo è tutto. E dietro la curva di ogni strada e fiume, tedeschi sempre più forti aspettavano i soldati alleati. Gli inglesi dell'8a armata camminavano sempre più lenti, e alla fine rimasero bloccati sulla cosiddetta linea invernale - difensiva tedesca posizioni, i cui elementi principali erano i fiumi di montagna Garigliano e Sangro e le vette degli Appennini. Le posizioni sui passi montani nel tempo autunnale sembravano inespugnabili per gli Alleati, quindi le loro forze divergevano in entrambe le direzioni: la 5a armata americana stava sfondando le posizioni nemiche lungo le rive del Mar Tirreno e l'8a Armata britannica avanzava lungo l'Adriatico. Gli Appennini che si trovavano tra di loro impedirono completamente la cooperazione tra di loro e ogni tentativo si concluse con pesanti perdite nel fuoco dei paracadutisti tedeschi e dei fucilieri alpini che controllano le montagne. Anche i tedeschi nell'Italia meridionale stavano guadagnando forza grazie alle nuove divisioni che arrivavano dal nord del paese e dalla Germania. orecchio al comando alleato che l'eliminazione dell'Italia come alleata del Terzo Reich solo per un momento scosse le truppe tedesche ancora potenti e indurite dalla battaglia. Si decise di chiamare rinforzi: il Corpo di spedizione francese, tra gli altri, fu trasportato dal Nord Africa e si decise che anche il 2 ° Corpo polacco sarebbe stato trasferito in Italia. Inoltre, è stata introdotta la 15a flotta aerea strategica.
      </p>
      <h4 class="fw-bold">Million Dollar Mountain</h4>
      <p>
        Uno dei corrispondenti di guerra britannici scrisse dall'Italia alla fine del 1943: "I nostri uomini costrinsero Sangro, ma dietro Sangro c'era Moro, dietro Moro c'era Foro, e dietro Foro c'era Pescara ...". E non c'era fine in vista. Tuttavia, prima che l'VIII Armata raggiungesse il fiume Pescara e l'omonima città sulla costa adriatica, dovette conquistare il piccolo comune di Ortone. Questa sentenza cadde sulla Prima Divisione canadese, ei canadesi furono i primi a scoprire cosa aveva in serbo il nemico sulla strada per Roma. Il veterano e scrittore britannico della Seconda Guerra Mondiale ha descritto la battaglia in questo modo: “Dovevi conquistare ogni strada, ogni casa e persino ogni piano qui. [I canadesi - PK] Ci sono voluti […] questa settimana, hanno passato il Natale così e alla fine hanno vinto. Ma quella fu la fine dello sforzo dell'8a armata. Pescara era a dieci miglia, il tempo e il terreno erano peggiori che mai e l'esercito dovette abbandonare temporaneamente l'ancora. "Sul lato opposto della penisola appenninica marciava la 5a armata americana. Gli americani, come gli inglesi, combattevano fango e un nemico sempre più duro. Riuscirono a raggiungere Monte Camino con grande difficoltà. Durante i combattimenti per questa collina, l'artiglieria sparò 200.000 proiettili. Così i soldati americani la chiamarono Million Dollar Mountain. Il 15 gennaio 1944, nell'area della strada Napoli - Roma, conquistarono il Monte Trocchio. Quindi, contando lo spazio e il tempo dalla conquista di Napoli, Gli Alleati impiegarono due mesi a percorrere diverse decine di chilometri ... E davanti a loro si ergevano le catene montuose della Maiella, degli Abruzzi e degli Aurunci, che dividevano la penisola dal Mar Adriatico al Mar Tirreno.Qui i tedeschi scolpirono e cementarono due potenti linee di difesa: Gustav e Hitler L'epicentro dell'Inferno attendeva gli Alleati a Cassino e dintorni.
      </p>
